import React from 'react';
import { Button, Popconfirm, Icon } from 'antd';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import * as creators from '../store/creators';

function Oper(props) {
  const fontSmall = { fontSize: "12px", marginLeft: "10px" };
  return (
    <div>
      <Link
        to={{
          pathname: "/platSetting/add",
          state: { editRecord: props.record }
        }}
      >
        <Button
          style={fontSmall}
          type="primary"
          size="small"
          ghost
        ></Button>
      </Link>
      <Popconfirm
        placement="left"
        title="?"
        onConfirm={() => {
          const data = { id: props.record.id }
          props.delete({ props, data })
        }}
        okText=""
        icon={<Icon type="question-circle" />}
        cancelText="">
        <Button style={fontSmall} type="danger" size="small" ghost></Button>
      </Popconfirm>
    </div >
  )
}

const mapDispatch = dispatch => ({
  delete: value => {
    const action = creators.deleteAction(value);
    dispatch(action);
  },
})

export default connect(null, mapDispatch)(Oper);
